<script>
  import {
    Message
  } from 'element-ui'
  import {jsonToString} from "../../utils/api";

  export default {
    data() {
      return {

        activeNames: ['1'],
        activeName: '1',
        lastPlanDetail: {
          startNo: 'J00000001',
          endNo: "J00000100"
        },
        lastPlanproductionPlanDetails: [{
          startNo: 'J00000001',
          endNo: "J00000100"
        }, {
          startNo: 'K00000001',
          endNo: "K00000100"
        }],
        // 月份使用？
        pickerOptions2: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        value4: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
        value5: '',
        //***********************************________*******************************
        keywords: '',
        tableLoading: false,
        advanceSearchViewVisible: false,
        rules: {
          // note: [{required: true, message: '必填:备注', trigger: 'blur'}],
        },
        types: 2,
        midProduct: [],
        plans: [],
        clients: [],
        units: [],
        selectsales: [],
        dialogTitle: '',
        currentPage: 1,
        totalCount: -1,
        // 原材料
        totalMaterialCount: -1,
        keyMaterialwords: '',
        currentMaterialPage: 1,
        plan: {
          id: 0,
          notes: '', //整单备注
          productionPlanNo: '', //整单编号
          productionPlanDetails: '',
        },
        detail: {
          id: 0,
          actualQuantity: '',
          salesPlanId: '',
          note: '',
          salesPlan: {}, //销售计划
          serialNumbers: [],
          type: 'new',
          status: 1,
          productionPlanSerialNumber: '',
          productionPlanId: 0,
        },
        serialNumbers: [],
        serialNumber: {
          sid: 1,
          id: 0,
          startNo: '',
          endNo: '',
          note: '',
          clientId: 0,
          productId: 0,
          type: "new"
        },
        productionPlanDetails: [],
        dialogFormVisible: false,
        currentRowData: {}, //当前选中行数据
        currentIndex: '', //当前选中行号
        depTextColor: '',
        showOrHidePop: false,
        showHidePop: false,
        defaultProps: {
          label: 'name',
          isLeaf: 'leaf',
          children: 'child'
        },
        maxSid: 0,
        currentDeleteData: [],
        materials: [],
        materialList: [],
        products: [],
        dialogMaterialVisible: false,
        dialogMaterialTitle: '请选择销售计划',
        selectSalePlan: {}, //当前选择的原料
        selectedM: {},
        filterText: '',
        lastPlanDetail: '', //最后一次计划详情;
        crrentSelectShop: {}, //当前选中的公司；
        isCurrentShop: true,
        isprarent: false,
        sugestParentstartNo: '', //..公司
        sugestParentEndNo: '', //公司结束编号
        sugestSelfEndNumbers: '',
        sugestSelfNumbers: '', //仅子公司
        currentParentShop: {},
        currentParentShops: [],
        currentShop: {},
        currentShops: [],
        serialNuberDeleted: [], //删除编号集合
        serialNuberUpdate: [], //更新编号集合
        serialNuberList: [], //编号集合
        serialNuberOld: [],//原编码集合；
        serialNuberNew: [],//新增数组
        planDetails: [],
        dialogPlanVisible: false,
        dialogPlanTitle: '生产计划',
        currntPlanRow: {},
        codeLen: 1,
        isEdite: true,
        currentSalePlan: {},
        selectedStep: {},
        manageStatus: 1,
        activePanel: '1',
      }
    },
    mounted: function () {
      this.initData();
    },

    methods: {
      handleClick() {
        console.log(this.activePanel);
        switch (this.activePanel) {
          case "1":
            this.manageStatus = 1;
            break;
          case "2":
            this.manageStatus = 2;
            break;
          case "3":
            this.manageStatus = 3;
            break;
          case "4":
            this.manageStatus = 4;
            break;
          case "5":
            this.manageStatus = '';
            break;
          default:
            break;
        }
        this.loadData();
      },
      //选择使用该编码；
      useCurrentNo(mo) {
        this.serialNumbers[0].startNo = this.sugestStartNo(mo.endNo, 2);
        this.serialNumbers[0].note = '';
        this.serialNumbers[0].clientId = this.crrentSelectShop.id;
        this.serialNumbers[0].productId = this.detail.proId;
        if (this.detail.actualQuantity) {
          this.serialNumbers[0].endNo = this.sugestStartNo(this.sugestStartNo(mo.endNo, 2), this.detail.actualQuantity);
        }
      },
      plandetailStatus(row) {
        let str = "";
        switch (row.status) {
          case 1:
            str = '新建';
            break;
          case 2:
            str = '开始生产';
            break;
          case 3:
            str = '生产完成';
            break;
          case 4:
            str = '生产撤销';
            break;
          default:
            break;
        }
        return str;
      },
      //编码长度设置
      codeLenSet(e) {
        let that = this;
        console.log(e);
        this.codeLen = e;
        if (this.serialNumbers.length) {
          this.serialNumbers.forEach(function (val) {
            if (val.startNo !== '' && val.startNo != undefined) {
              val.startNo = that.sugestStartNo(val.startNo, 1);
            }
            if (val.endNo !== '' && val.endNo !== undefined) {
              val.endNo = that.sugestStartNo(val.endNo, 1);
            }
          })
        }


      },
      // 生产计划撤销
      cancelPlanOpt() {
        this.dialogFormVisible = false;
        this.plan = {
          notes: '', //整单备注
          productionPlanNo: '', //整单编号
          productionPlanDetails: '',
        };
        this.selectSalePlan = {};
        this.loadData();
      },
      editePlan(index, row) {
        this.dialogTitle = "修改计划单";
        let that = this;
        this.plan = row;
        let plans = _.cloneDeep(row);
        let currentDetail = plans.details;

        currentDetail.forEach(v => {
          delete v.salesPlan;
        });
        let str = jsonToString(currentDetail);
        this.plan.productionPlanDetails = str;
        that.dialogPlanVisible = true;
      },
      //生产计划新增\修改
      addPlan(addPlanForm) {
        let _this = this;
        this.$refs[addPlanForm].validate((valid) => {
          if (valid) {
            if (_this.plan.id) {
              //更新
              _this.tableLoading = true;
              console.log(_this.plan);
              _this.postRequest("/productionplan/update", _this.plan).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  _this.dialogPlanVisible = false;

                  _this.loadData();
                  _this.emptyData();
                  _this.emptyDetail();
                  _this.clearSerialNumbers();
                }
              })
            } else {
              //添加
              _this.tableLoading = true;
              console.log(_this.plan);
              _this.postRequest("/productionplan/add", _this.plan).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  _this.dialogPlanVisible = false;
                  _this.loadData();
                  _this.emptyData();
                  _this.emptyDetail();
                  _this.clearSerialNumbers();
                }
              })
            }
          } else {
            return false;
          }
        });

      },
      // 编号***********
      serialNumberAdd() {
        let sid = 0;
        if (this.serialNumbers.length) {
          let maxId = 0;
          //找出最大的Id+1
          this.serialNumbers.forEach(function (v, i, arr) {
            maxId = v.sid > maxId ? v.sid : maxId;
          });
          sid = maxId + 1;
        } else {
          sid = 1;
        }
        this.serialNumbers.push({
          sid: sid,
          id: 0,
          note: '',
          startNo: '',
          endNo: '',
          clientId: 0,
          productId: 0,
          type: "new"
        });
      },
      deletSerialNo(index) {
        let deletData = this.serialNumbers.splice(index, 1);
        let conditions = deletData[0].endNo === '' ||
          deletData[0].startNo === '' ||
          deletData[0].type === 'new';
        if (!conditions) {
          deletData[0].type = 'delete';
          this.serialNuberDeleted.push(deletData[0])
        }
