    justify-content: stretch;

  }


  .in-plans-show:hover {
    border-color: red;
    position: relative;
  }


  .in-plans-show {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 6px;
    margin-bottom: 5px;
    border: 1px solid #ccc;
    text-align: start;
    height: inherit;
    overflow-y: hidden;
    overflow: auto;
  }

  .text-alight {
    margin-left: 5px;
  }
</style>



